import { Dialog } from '@ui/overlays/dialog/dialog';
import { DialogHeader } from '@ui/overlays/dialog/dialog-header';
import { Trans } from '@ui/i18n/trans';
import { DialogBody } from '@ui/overlays/dialog/dialog-body';
import { TextField } from '@ui/forms/input-field/text-field/text-field';
import { SearchIcon } from '@ui/icons/material/Search';
import { useState } from 'react';
import { useTrans } from '@ui/i18n/use-trans';
import { message } from '@ui/i18n/message';
import { Avatar } from '@ui/avatar/avatar';
import { IllustratedMessage } from '@ui/images/illustrated-message';
import { SvgImage } from '@ui/images/svg-image';
import teamSvg from '../admin/roles/team.svg';
import { useDialogContext } from '@ui/overlays/dialog/dialog-context';
import { useNormalizedModels } from '../ui/normalized-model/use-normalized-models';
import { DialogFooter } from '@ui/overlays/dialog/dialog-footer';
import { Button } from '@ui/buttons/button';
import clsx from 'clsx';
export function SelectUserDialog({
  multiple = false,
  endpoint = 'normalized-models/user'
}) {
  const {
    close
  } = useDialogContext();
  const [searchTerm, setSearchTerm] = useState('');
  const {
    trans
  } = useTrans();
  const query = useNormalizedModels(endpoint, {
    query: searchTerm,
    perPage: 14
  });
  const users = query.data?.results || [];
  const [selectedUsers, setSelectedUsers] = useState([]);
  const emptyStateMessage = <IllustratedMessage className="pt-20" size="sm" title={<Trans message="No matching users" />} description={<Trans message="Try another search query" />} image={<SvgImage src={teamSvg} />} />;
  return <Dialog>
      <DialogHeader>
        {multiple ? <Trans message="Select users" /> : <Trans message="Select a user" />}
      </DialogHeader>
      <DialogBody>
        <TextField autoFocus size="sm" className="mb-20" startAdornment={<SearchIcon />} placeholder={trans(message('Search for user by name or email'))} value={searchTerm} onChange={e => {
        setSearchTerm(e.target.value);
      }} />
        {!query.isLoading && !users.length && emptyStateMessage}
        <div className="grid grid-cols-2 gap-x-10">
          {users.map(user => <UserListItem isSelected={!!selectedUsers.find(u => u.id === user.id)} key={user.id} user={user} onSelected={user => {
          if (multiple) {
            if (selectedUsers.find(u => u.id === user.id)) {
              setSelectedUsers(selectedUsers.filter(u => u.id !== user.id));
            } else {
              setSelectedUsers([...selectedUsers, user]);
            }
          } else {
            close(user);
          }
        }} />)}
        </div>
      </DialogBody>
      {multiple && <DialogFooter>
          <Button onClick={() => close()}>
            <Trans message="Cancel" />
          </Button>
          <Button variant="flat" color="primary" onClick={() => close(selectedUsers)} disabled={!selectedUsers.length}>
            <Trans message="Select" />
          </Button>
        </DialogFooter>}
    </Dialog>;
}
function UserListItem({
  user,
  onSelected,
  isSelected
}) {
  return <div key={user.id} className={clsx('flex items-center gap-10 rounded border-1 p-10 outline-none ring-offset-4 focus-visible:ring', isSelected ? 'border-primary bg-primary/5 hover:bg-primary/10' : 'border-transparent hover:bg-hover')} role="button" tabIndex={0} onClick={() => {
    onSelected(user);
  }} onKeyDown={e => {
    if (e.key === 'Enter' || e.key === ' ') {
      e.preventDefault();
      onSelected(user);
    }
  }}>
      <Avatar src={user.image} label={user.name} circle />
      <div className="overflow-hidden">
        <div className="overflow-hidden text-ellipsis">{user.name}</div>
        <div className="overflow-hidden text-ellipsis text-muted">
          {user.description}
        </div>
      </div>
    </div>;
}