import React, { Children, cloneElement, isValidElement, useContext, useRef, useState } from 'react';
import clsx from 'clsx';
import { useLayoutEffect } from '@react-aria/utils';
import { getFocusableTreeWalker } from '@react-aria/focus';
import { TabContext } from '@ui/tabs/tabs-context';
export function TabPanels({
  children,
  className
}) {
  const {
    selectedTab,
    isLazy
  } = useContext(TabContext);

  // filter out falsy values, in case of conditional rendering
  const panelArray = Children.toArray(children).filter(p => !!p);
  let rendered;
  if (isLazy) {
    const el = panelArray[selectedTab];
    rendered = isValidElement(el) ? cloneElement(panelArray[selectedTab], {
      index: selectedTab
    }) : null;
  } else {
    rendered = panelArray.map((panel, index) => {
      if (isValidElement(panel)) {
        const isSelected = index === selectedTab;
        return cloneElement(panel, {
          index,
          'aria-hidden': !isSelected,
          className: !isSelected ? clsx(panel.props.className, 'hidden') : panel.props.className
        });
      }
      return null;
    });
  }
  return <div className={className}>{rendered}</div>;
}
export function TabPanel({
  className,
  children,
  index,
  ...domProps
}) {
  const {
    id
  } = useContext(TabContext);
  const [tabIndex, setTabIndex] = useState(0);
  const ref = useRef(null);

  // The tabpanel should have tabIndex=0 when there are no tabbable elements within it.
  // Otherwise, tabbing from the focused tab should go directly to the first tabbable element
  // within the tabpanel.
  useLayoutEffect(() => {
    if (ref?.current) {
      const update = () => {
        // Detect if there are any tabbable elements and update the tabIndex accordingly.
        const walker = getFocusableTreeWalker(ref.current, {
          tabbable: true
        });
        setTabIndex(walker.nextNode() ? undefined : 0);
      };
      update();

      // Update when new elements are inserted, or the tabIndex/disabled attribute updates.
      const observer = new MutationObserver(update);
      observer.observe(ref.current, {
        subtree: true,
        childList: true,
        attributes: true,
        attributeFilter: ['tabIndex', 'disabled']
      });
      return () => {
        observer.disconnect();
      };
    }
  }, [ref]);
  return <div tabIndex={tabIndex} ref={ref} id={`${id}-${index}-tabpanel`} aria-labelledby={`${id}-${index}-tab`} className={clsx(className, 'focus-visible:outline-primary-light')} role="tabpanel" {...domProps}>
      {children}
    </div>;
}